import { PARAM } from 'App';
import { RendererCanvas } from '../../../lib'

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;

    constructor(divHost: any) {
        this.divHost = divHost;

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';

        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.width = '1000px';
        div.style.height = '600px';
        this.divHost.appendChild(div);

        if (PARAM.demo) {
            this.renderer = new Renderer(div);

            let btn = document.createElement('div');
            div.appendChild(btn);
            btn.textContent = 'Reset';
            btn.onclick = () => {
                this.renderer.Build();
            };
            const cBox = getCheckBox('Connectivity');
            div.appendChild(cBox.div);
            cBox.checkBox.checked = false;
            cBox.checkBox.onchange = () => {
                console.log(cBox.checkBox.checked)
                if (cBox.checkBox.checked === true) {
                    this.renderer.connectionDiagonal = true;
                } else {
                    this.renderer.connectionDiagonal = false;
                }
            }

            btn = document.createElement('div');
            div.appendChild(btn);
            btn.textContent = 'Play and Stop toggle';
            btn.onclick = () => {
                this.renderer.playToggle = !this.renderer.playToggle;
            };

            const sl2 = getSlider('Heuristic intensity', 0, 1000);
            div.appendChild(sl2.div);
            sl2.slider.oninput = () => {
                const val = 1 / +sl2.slider.value - 0.001;
                this.renderer.graph.heuristicConst = val;
            };



        }
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

const renderCell = (ctx: CanvasRenderingContext2D, node: Node, col: string, cellSize: number) => {
    ctx.fillStyle = col;
    ctx.beginPath();
    ctx.rect(node.x * cellSize, node.y * cellSize, cellSize - 1, cellSize - 1);
    ctx.closePath();
    ctx.fill();
};

export class Renderer extends RendererCanvas {
    w: number = 0;
    h: number = 0;
    cols: number = 0;
    rows: number = 0;
    cellSize: number = 20;
    gridWidth: number = 0;
    gridHeight: number = 0;
    chanceOfWalls: number = 0.35;
    connectionDiagonal = false

    graph: PixelMap;

    playToggle = true;

    frame = 0;
    public divTextIteration: HTMLDivElement;

    constructor(div: any) {
        super(div);
        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // TODO
        // You code goes here for one time operation
        this.divTextIteration = document.createElement('div');
        div.appendChild(this.divTextIteration);

        this.Build();
        this.start();
    }
    public render(ctx: CanvasRenderingContext2D): void {
        if (this.playToggle) {
            const result = this.graph.propagation();
            this.divTextIteration.textContent = `${this.frame++} iterations`;
        }
        // const result = this.graph.gePath();
        if (this.graph.doneComputation) { this.Build(); }
        // console.log(result)

        for (let i = 0; i < this.cols; i++) {
            for (let j = 0; j < this.rows; j++) {
                const node = this.graph.grid[i][j];
                let color = 'rgb(190, 190, 190)';

                if (node.blocked) { color = `rgba(0, 0, 0, 0.9)`; }
                if (node === this.graph.endNode) { color = `rgba(255, 0, 255, 0.9)`; }

                renderCell(ctx, node, color, this.cellSize);

            }
        }

        for (let i = 0; i < this.graph.openNodes.length; i++) { // render open nodes
            const node = this.graph.openNodes[i];
            const color = 'rgb(0, 0, 255)';
            renderCell(ctx, node, color, this.cellSize);
        }

        for (let i = 0; i < this.graph.closedNodes.length; i++) { // visited nodes
            const node = this.graph.closedNodes[i];
            const color = 'rgb(255, 0, 0)';
            renderCell(ctx, node, color, this.cellSize);
        }

        for (let i = 0; i < this.graph.path.length; i++) { // path
            const node = this.graph.path[i];
            const color = 'rgb(0, 255, 0)';
            renderCell(ctx, node, color, this.cellSize);
        }

        for (let i = 0; i < this.cols; i++) { // connection
            for (let j = 0; j < this.rows; j++) {
                const node = this.graph.grid[i][j];
                if (node.blocked) { continue; }
                for (let k = 0; k < node.neighbors.length; ++k) {
                    ctx.strokeStyle = '#fff';
                    ctx.lineWidth = 0.58;
                    ctx.beginPath();
                    ctx.moveTo(this.cellSize * node.x + this.cellSize * 0.5, this.cellSize * node.y + this.cellSize * 0.5);
                    ctx.lineTo(this.cellSize * node.neighbors[k].x + this.cellSize * 0.5, this.cellSize * node.neighbors[k].y + this.cellSize * 0.5);
                    ctx.stroke();
                }

            }
        }
    }
    public Build() {
        this.frame = 0;
        this.w = this.canvas.width;
        this.h = this.canvas.height;
        this.cols = Math.floor(this.w / this.cellSize);
        this.rows = Math.floor(this.h / this.cellSize);
        this.gridWidth = this.w;
        this.gridHeight = this.h;


        this.graph = new PixelMap();
        this.graph.connectionDiagonal = this.connectionDiagonal;

        //Build navigation grid
        for (let i = 0; i < this.cols; i++) {
            for (let j = 0; j < this.rows; j++) {
                if (this.graph.grid[i] === undefined)
                    this.graph.grid[i] = [];

                const node = new Node(i, j);
                if (Math.random() < this.chanceOfWalls) { node.blocked = true; }
                this.graph.grid[i][j] = node;
            }
        }

        this.graph.startNode = this.getRandomCell();
        this.graph.startNode.blocked = false;

        this.graph.endNode = this.getRandomCell();
        this.graph.endNode.blocked = false;

        //find neighbors
        for (let i = 0; i < this.cols; i++) {
            for (let j = 0; j < this.rows; j++) {
                this.graph.buildConnection(this.graph.grid[i][j], this.cols, this.rows);
            }
        }

        this.graph.openNodes.push(this.graph.startNode);

    }
    getRandomCell() {
        return this.graph.grid[Math.floor(Math.random() * this.cols)][Math.floor(Math.random() * this.rows)];
    }
}

class PixelMap {
    public grid: Node[][] = [];
    public openNodes: Node[] = [];
    public closedNodes: Node[] = [];
    public startNode: Node;
    public endNode: Node;
    public path: Node[] = [];
    public heuristicConst = 1;
    public doneComputation: boolean = false;
    public connectionDiagonal = false;
    // constructor() {
    //     // TODO
    // }
    public buildConnection = (node: Node, cols: number, rows: number) => {
        if (node.blocked) { return; }

        let x = node.x;
        let y = node.y;

        if (x < cols - 1) {
            if (!this.grid[x + 1][y].blocked) {
                node.neighbors.push(this.grid[x + 1][y]);
            } // right
        }

        if (y < rows - 1) {
            if (!this.grid[x][y + 1].blocked) {
                node.neighbors.push(this.grid[x][y + 1]);
            } // bottom
        }

        if (x > 0) {
            if (!this.grid[x - 1][y].blocked) {
                node.neighbors.push(this.grid[x - 1][y]);
            } // left
        }

        if (y > 0) {
            if (!this.grid[x][y - 1].blocked) {
                node.neighbors.push(this.grid[x][y - 1]);
            } // top
        }
        if (this.connectionDiagonal) {
            console.log(this.connectionDiagonal);
            if (x > 0 && y > 0) {
                if (!this.grid[x - 1][y - 1].blocked) {
                    node.neighbors.push(this.grid[x - 1][y - 1]);
                }
            } // left up
            if (x < cols - 1 && y < rows - 1) {
                if (!this.grid[x + 1][y + 1].blocked) {
                    node.neighbors.push(this.grid[x + 1][y + 1]);
                }
            } // right bottom
            if (x > 0 && y < rows - 1) {
                if (!this.grid[x - 1][y + 1].blocked) {
                    node.neighbors.push(this.grid[x - 1][y + 1]);
                }
            } // left bottom
            if (x < cols - 1 && y > 0) {
                if (!this.grid[x + 1][y - 1].blocked) {
                    node.neighbors.push(this.grid[x + 1][y - 1]);
                }
            } // right top
        }
    }
    public gePath() {
        let result = undefined;
        let count = this.grid[0].length * this.grid.length
        while (count--) {
            result = this.propagation();
            if (result !== undefined && result.length) {
                return result;
            }
        }
        return result;
    }
    public propagation() {
        // console.log('-----');
        if (this.openNodes.length > 0) {
            //find the lowest f in open nodes
            let lowestF = 0;
            for (let i = 0; i < this.openNodes.length; i++) {
                if (this.openNodes[i].f < this.openNodes[lowestF].f) {
                    lowestF = i;
                }

                if (this.openNodes[i].f === this.openNodes[lowestF].f) {
                    if (this.openNodes[i].g > this.openNodes[lowestF].g) {
                        lowestF = i;
                    }
                }
            }
            let current: Node = this.openNodes[lowestF];

            if (current === this.endNode) {
                //reached the end node so wait a sec and restart
                this.doneComputation = true;
                return this.path;
            }

            //calculate path
            this.path = [];
            let temp = current;
            while (temp.previous) {
                this.path.push(temp.previous);
                temp = temp.previous;
            }
            // console.log(this.path.length);

            this.openNodes.splice(this.openNodes.indexOf(current), 1);
            this.closedNodes.push(current);

            let neighbors = current.neighbors;
            for (let i = 0; i < neighbors.length; i++) {
                let neighbor = neighbors[i];

                if (!this.closedNodes.includes(neighbor) && !neighbor.blocked) {
                    let tempG = current.g + heuristic(neighbor, current) * this.heuristicConst;

                    if (!this.openNodes.includes(neighbor)) {
                        this.openNodes.push(neighbor);
                    }
                    else {
                        continue;
                    }

                    neighbor.g = tempG;
                    neighbor.h = heuristic(neighbor, this.endNode);
                    neighbor.f = neighbor.g + neighbor.h;
                    neighbor.previous = current
                }
            }
            return undefined;
        } else {
            this.doneComputation = true;
            return this.path;
        }
    }
}
class Node {
    public f = 0;
    public h = 0;
    public g = 0;

    public x = 0;
    public y = 0;
    public z = 0;
    public neighbors: Node[] = [];
    public previous = undefined;
    public blocked = false;
    constructor(x: number, y: number) {
        this.f = 0;
        this.h = 0;
        this.g = 0;
        this.x = x;
        this.y = y;
        this.neighbors = [];
        this.blocked = false;
    }
}
const heuristic = (a: Node, b: Node) => {
    let d = 0;
    // d = distanceManhattan(a.x, a.y, a.z, b.x, b.y, b.z); // find the Manhattan distance (best for grids)
    d = distanceEuclidean(a.x, a.y, a.z, b.x, b.y, b.z); // euclidean - find the direct distance
    return d;
}
const distanceManhattan = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
    return Math.abs(x0 - x1) + Math.abs(y0 - y1) + Math.abs(z0 - z1);
}
const distanceEuclidean = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
    return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1) + (z0 - z1) * (z0 - z1));
}



